@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "";
  font-display: swap;
  src: url("../fonts/.woff2") format("woff2"), url("../fonts/.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff; }

a {
  color: #ffffff;
  text-decoration: none;
  cursor: pointer; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  overflow: hidden;
  background-color: #231320ef;
  background-image: url("../img/content/background.jpg");
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: cover; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  text-transform: lowercase;
  font-weight: 400; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.scroll-lock-ios {
  position: fixed;
  overflow: hidden; }

.scroll-lock {
  overflow: hidden; }

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .no-scrollbar::-webkit-scrollbar {
    display: none; }

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 40px; }
  @media (max-width: 1199px) {
    .container {
      padding: 0 30px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 20px; } }

.promo h1 {
  margin-top: 50px; }

.header {
  display: flex;
  justify-content: flex-end;
  margin: 15px 20px; }
  @media (max-width: 767px) {
    .header {
      flex-direction: column; } }

.header a {
  margin-right: 50px;
  text-decoration: none;
  background-repeat: no-repeat;
  background-position: left;
  padding-top: 10px;
  padding-bottom: 10px;
  transition: opacity 0.3s ease; }
  .header a:hover {
    opacity: 0.5; }

.header__email {
  padding-left: 30px;
  background-image: url("../img/icon-email-header.svg"); }

.header__phone {
  padding-left: 25px;
  background-image: url("../img/icon-phone-header.svg"); }

.projects__list {
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 20px;
  list-style: none;
  margin: 0;
  padding: 0; }
  @media (max-width: 1199px) {
    .projects__list {
      grid-template-columns: 1fr 1fr; } }
  @media (max-width: 767px) {
    .projects__list {
      grid-template-columns: 1fr; } }

.projects__item {
  display: flex;
  flex-direction: column;
  padding: 30px;
  border: 2px solid #ffffff;
  border-radius: 10px;
  cursor: pointer;
  transition: box-shadow 0.3s ease, opacity 0.3s ease; }
  .projects__item:hover {
    box-shadow: 3px 3px 5px #ffffff;
    opacity: 0.8; }

.projects__item h3 {
  min-height: 50px;
  margin: 0 0 15px;
  text-align: center; }

.projects__item p {
  margin: 10px 0 0; }

.footer {
  display: flex;
  justify-content: flex-end;
  margin: 30px 0; }
  @media (max-width: 767px) {
    .footer {
      flex-direction: column; } }

.footer a {
  margin-right: 50px;
  text-decoration: none;
  background-repeat: no-repeat;
  background-position: left;
  padding-top: 10px;
  padding-bottom: 10px;
  transition: opacity 0.3s ease; }
  .footer a:hover {
    opacity: 0.5; }

.footer__email {
  padding-left: 30px;
  background-image: url("../img/icon-email-header.svg"); }

.footer__phone {
  padding-left: 25px;
  background-image: url("../img/icon-phone-header.svg"); }

.scills__list {
  list-style-type: '-';
  column-count: 4; }
  @media (max-width: 1199px) {
    .scills__list {
      column-count: 2; } }
  @media (max-width: 767px) {
    .scills__list {
      font-size: 12px;
      column-count: 1; } }
